.FAQ-banner{
    height: 304px;
    background-image: url('../images/week4/72@2x.png');
    background-size: cover;
    background-position:  center;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;

    @include pad{
        background-position: 30% center;
        background-size: cover;
    }

    @include phone{
        height: 143px;
        background-position: 30% center;
        background-size: cover;
    }

    h1{
        font-size: 64px;
        line-height: 80px;

        @include pad{
            font-size: 48px;
            line-height: 80px;
        }

        @include phone{
            font-size: 28px;
            line-height: 40px;
        }
    }

    .quesition-icon{
        width: 58px;
        height: 58px;

        @include phone{             
            width: 24px;
            height: 24px;        
        }
    }
}

.FAQ-contain{
    h2{
        color: #000;
        font-size: 200%;
        line-height: 63px;
        margin-top: 88px;
        margin-bottom: 16px;

        @include phone{
            font-size: 20px;
            line-height: 28px;
            margin-top: 24px;
            margin-bottom: 16px;
        }
    }
}

.question-lists{
    width: 100%;
    margin-bottom: 156px;

    @include pad{
        margin-bottom: 104px;
    }

    @include phone{
        margin-bottom: 56px;
    }
}

.question-list-item{ 
    position: relative;
    border: 1px solid #000;

    label{
        margin-bottom: 0;
        cursor: pointer;
    }
    
    .quesition-title{
        font-size: 20px;
        padding: 22px 38px; 
        font-weight: 500;

        @include pad{
            padding: 16px 24px;
        }

        @include phone{
            @include font(1);
            padding: 16px;
        }
    }

    input[type=checkbox]{
        appearance: none;
        outline: none;
        width: 39px;
        height: 24px;
        background-size: cover;
        background-position: center center;
        background-image: url("../images/week4/ic_keyboard_arrow_down_24px.svg");
        position: absolute;
        right: 16px;
        top: 24px;
        cursor: pointer;

        @include pad{
            top: 20px;
        }

        @include phone{
            top: 16px;
        }
    }

    .slide-content{
        display: none;
        font-size: 16px;
        line-height: 24px;
    }

    input[type=checkbox]:checked{
        transform: rotate(180deg);
        outline: none;
    }

    input[type=checkbox]:checked + .slide-content{
        display: block;
        border-top: 1px solid #000;
        background-color: #F2F2F2;
        padding: 22px 38px;

        @include pad{
            padding: 16px 16px;
            
        }
    }
}
